				footer-bg-variant="white"
				
				v-on:click="showUser(member)"
				no-body
				>
				<div class="card-content ">
					<div class="row">
					
						<div class="col-4 my-3">
							<div class=" ca-img-thumbnail" :style='generateBgImage(member.profileImg)'>	
										
							</div>
						</div >
						<div class="col-8 my-3 " style="position: relative">
							<b-badge 
								pill 
								variant="primary" 
								v-if="member.you"  
								style="align-self: flex-start"	
								class=" float-right"
							>
								YOU
							</b-badge>
							<div class="" style="position: absolute; height:100%; display: flex;">
								<h5 class="" style=" align-self: flex-end; " >{{ member.username }}</h5>
							</div>
							
							
						</div>
					</div>
					<hr>
					<span>{{ member.label }}<span class="text-muted"> since {{ formatDate(member.createdAt) }}</span></span>

				</div>
				
				

			</b-card>

		</b-card-group>
		<div class="text-center py-5 text-secondary"  v-else>
