<script>

import CONST from '../utils/constants.js';



export default {
	name: 'CAMembersDeck',
	props: ['members'],
	components: {
		
	},
	data() {
		return {


		}
	},
	computed:{

		_members(){

			window.console.log(">> CAMembersDeck > list of members: ",this.members);

			window.console.log("CAMembersDeck: ",this.members);

			return this.members;
		}
		


	},
	methods: {
		showUser(user) {
			this.$router.push({ name: 'member', params: { id: user._id } })
		},
		formatDate(date){
			var d = new Date(date)
			return (d.getDate()+" "+CONST.months[d.getMonth()]+" "+d.getFullYear())		
		},
		pathToImage(image){
			const remotePath =  this.$API.replace("api","");
			if(image){
				return remotePath+image;
			}else{
				return remotePath+"uploads/images/defaultUser.png";
			}
			
		},
		generateBgImage(image){
			
			return `background-image: url('${this.pathToImage(image)}')`;
		
		}
	},
	created() {
		//this.update()
	},
	update(){

